                    .right-facilities {
                        @include facilities-design();
                        margin-right: 25px;
                        margin-top: 10px;
                    }
                }

                .itineraries-data {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    padding: 0px 25px 20px 25px;
                    margin-top: 10px;
                    //border: 1px solid black;
                    height: fit-content;

                    .left-iti,
                    .right-iti {
                        width: 80%;
                        display: flex;
                        flex-direction: column;
                        justify-content: left;
                        text-align: left;

                        .departure-airport {
                            font-size: 35px;
                            font-weight: 600;
                            font-family: 'Open Sans', sans-serif;
                            color: rgb(39, 39, 39);
                        }

                        .departure-date {
                            font-size: 12px;
                            font-weight: 600;
                            color: rgba(0, 0, 0, 1);
                        }

                        .departure-time {
                            font-size: 16px;
                            font-weight: 600;
                            color: rgb(29, 29, 29);
                        }
                    }

                    .middle-iti {
                        width: 100%;

                        .icon {
                            position: relative;
                            top: -39.55%;
                            width: 24px;
                            height: 24px;
                            rotate: 90deg;
                            color: rgb(48, 48, 48);
                            margin-left: 47%;
                            background-color: white;
                            padding: 0 2px;
                        }

                        .visual-flight-meta-data {
                            display: flex;
                            flex-direction: row;

                            .line {
                                border-top: 1px solid rgb(48, 48, 48);
                                margin: 70px auto;
                                width: 99%;
                            }

                            .left-dot {
                                height: 5px;
                                width: 5px;
                                border-radius: 100%;
                                margin-top: 67px;
                                background-color: $visual-color;
                            }

                            .right-dot {
                                height: 8px;
                                width: 8px;
                                border-radius: 100%;
                                margin-top: 67px;
                                background-color: $visual-color;
                            }
                        }
                    }

                    .total-flight-duration {
                        margin-top: -90px;
                        text-align: center;
                        color: rgb(48, 48, 48);
                        font-size: 10px;
                        font-weight: 600;
                        font-family: 'Open Sans', sans-serif;
                    }

                    .right-iti {
                        justify-content: right;
                        text-align: right;
                    }
                }

                .details {
                    height: fit-content;
                    width: 95%;
                    padding-top: 20px;
                    border-top: 1px solid rgba(0, 0, 0, 0.096);
                    margin: 20px auto 30px auto;

                    .carrier-detail {
                        height: 50px;
                        padding: 10px 0px;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;

                        .travel-date {
                            font-size: 16px;
                            font-weight: 600;
                            color: rgba(0, 0, 0, 0.801);

                            .icon {
                                font-size: 22px;
                                padding-top: 10px;
                            }
                        }

                        .company {
                            display: flex;
                            flex-direction: row;
                            justify-content: start;
                            align-items: center;

                            .logo {
                                height: 33px;
                                width: 33px;
                            }

                            .name {
                                font-size: 15px;
                                font-weight: 600;
                                color: rgba(0, 0, 0, 1);
                                margin-left: 10px;
                                margin-top: -20px;
                            }
                        }

                        .model {
                            font-size: 12px;
                            font-weight: 500;
                            margin-left: 43px;
                            margin-top: -15px;
                            width: fit-content;
                        }
                    }

                    .travel-detail {
                        height: 130px;
                        margin: 0 auto;
                        width: 100%;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        padding-left: 30px;
                        border-left: 5px solid rgba(0, 38, 255, 0.082);
                        margin-top: 20px;
                        margin-bottom: 50px;

                        .demographic {
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-start;
                            color: #001e5e;
                            padding: 10px;
                            border-radius: 5px;
                            flex-basis: 15%;

                            .airport-code {
                                font-size: 30px;
                                font-weight: 600;
                            }

                            .terminal {
                                font-size: 12px;
                                font-weight: 500;
                            }

                            .departure-date {
                                font-family: 'Open Sans', sans-serif;
                                font-size: 12px;
                                font-weight: 600;
                            }

                            .departure-time {
                                font-size: 16px;
                                font-weight: 600;
                            }
                        }

                        .visual {
                            height: 100%;
                            width: 100px;
                            display: flex;
                            flex-basis: 20%;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;

                            .icon {
                                position: relative;
                                rotate: 90deg;
                                left: 40px;
                                top: 27.9px;
                                height: 20px;
                                width: 20px;
                                z-index: 10;
                                color: rgb(40, 0, 184);
                                background-color: white;
                            }

                            .lineJourney {
                                height: 1.5px;
                                width: 100px;
                                position: relative;
                                top: -20px;
                                background-color: #002677;
                                margin-top: 30px;
