            height: 78px;
            width: 100vw;
            position: relative;
            top: 64px;
            margin: 0 auto;
            background-color: #ffffff;
            border-top: 1px solid $search-box-border-color;
            border-bottom: 1px solid $search-box-border-color;
            box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);

            .search-inputs {
                width: 100vw;
                height: 100%;
                margin: 0 auto;
                display: flex;
                flex-direction: row;
                justify-content: center;

                .overlaySearch {
                    background-color: transparent;
                    width: 80vw;
                    height: 100%;
                    position: absolute;
                    z-index: 5;
                }

                .locFirst,
                .locLast,
                .lastPass,
                .dateLast {
                    border-left: 1px dotted $search-box-border-color;
                }

                .lastPass {
                    border-right: 1px dotted $search-box-border-color;
                }

                .dropdownListSearch {
                    position: absolute;
                    top: 90px;
                    width: 350px;
                    background-color: rgba(255, 255, 255, 1);
                    box-shadow: 0 1px 7px 0 rgba(0, 0, 0, 0.4);
                    z-index: 10;
                    overflow-y: auto;
                    overflow-x: hidden;
                    max-height: 200px;
                    border-radius: 5px;

                    .dropdownListSearch::-webkit-scrollbar {
                        width: 5px;
                    }

                    .dropdownListSearch::-webkit-scrollbar-track {
                        background: transparent;
                    }

                    .dropdownListSearch::-webkit-scrollbar-thumb {
                        background-color: rgba(0, 0, 0, 0.3);
                        border-radius: 5px;
                    }

                    .searchBox {
                        position: sticky;
                        top: 0;
                        height: 35px;
                        width: 100%;
                        display: flex;
                        align-items: center;
                        border: none;
                        border-top-right-radius: 5px;
                        border-top-left-radius: 5px;
                        background-color: white;
                        border-bottom: 1px solid rgba(128, 128, 128, 0.267);

                        #searchIcon {
                            height: 24px;
                            width: 24px;
                            margin: 0px 10px;
                            color: var(--primary-color);
                        }

                        input {
                            height: 100%;
                            width: 100%;
                            border: none;
                            padding: 2px 0px;
                            font-size: 0.9rem;
                            font-weight: 400;
                            color: rgb(36, 36, 36);
                            letter-spacing: 1px;

                            &:focus {
                                outline: none;
                            }
                        }
                    }

                    .dropdownItem {
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        padding: 0.2rem 0.5rem;
                        font-size: 0.8rem;
                        font-weight: 500;
                        cursor: pointer;

                        &:hover {
                            background-color: rgb(243, 243, 243);
                        }
                    }

                    .MetaLocation {
                        display: flex;
                        flex-direction: column;
                        align-items: start;
                        justify-content: center;
                        width: 88%;
                        overflow-x: hidden;

                        .airport-name {
                            font-size: 0.65rem;
                        }
                    }
                }

                .dataBox {
                    display: flex;
                    flex-direction: column;
                    align-items: start;
                    justify-content: space-between;
                    width: 20vw;
                    height: 100%;
                    padding: 5px 15px;

                    cursor: pointer;

                    .constant-tag {
                        font-size: calc(0.6rem + 0.1vw);
                        font-weight: 500;
                        color: rgba(75, 75, 75, 0.637);
                        position: relative;

                        #dropdownArrow {
                            height: 20px;
                            width: 20px;
                            position: absolute;
                            top: -2px;
                            color: var(--greyText);
                        }
                    }

                    $middle-text-size: 0.6rem;

                    .Middle {
                        font-size: $middle-text-size;
                        font-weight: 500;
                        margin-top: 25px;
                        color: rgba(0, 56, 177, 0.863);
                    }

                    .Last {
                        font-size: $middle-text-size;
                        font-weight: 600;
                        margin-top: -40px;
                        // white-space: nowrap;
                        color: rgb(80, 80, 80);

                    }

                    .airport-code {
                        color: rgb(0, 34, 187);
                        font-size: $middle-text-size;
                    }
                }

                /* Customizing the header */
                .react-datepicker__header {
                    background-color: rgb(0, 91, 228);
                    color: white;
                }

                /* Customizing the current date */
                .react-datepicker__day--selected {
                    background-color: #34bd68;
                }

                /* Customizing the today highlight 
                .react-datepicker__day--today {
                    border: none;
                    border-radius: 50px;
                    background-color: #ffd28e;
                }*/

                /* Customizing the days of the week */
                .react-datepicker__day-name {
                    color: #e7e7e7dc;
                }

                /* Customizing Month and year title */
                .react-datepicker__current-month,
                .react-datepicker__current-year {
                    color: #e7e7e7;
                }

                /* Customizing the days of the month */
                .react-datepicker__month-dropdown,
                .react-datepicker__year-dropdown {
                    background-color: #333;
                    color: white;
                }

                /* Customizing hover state */
                .react-datepicker__day:hover {
                    border-radius: 50%;
                    background-color: lightgrey;
                }

                /* Customizing the arrows */
                .react-datepicker__navigation--previous,
                .react-datepicker__navigation--next {
                    top: 12px;
                    border-color: white;
                }

                .calendar {
                    border: 1px solid rgb(209, 209, 209);
                    padding: 3px 5px;
                    border-radius: 5px;
                    font-size: 15px;
                    letter-spacing: 1px;
                    position: relative;
                    top: -40px;
                    left: 25px;

                    :focus {
                        outline: none;
                    }
                }
            }
        }

        @mixin facilities-design() {
            color: rgba(36, 36, 36, 0.801);
            font-family: 'Open Sans', sans-serif;
            font-weight: 500;
            font-size: 11px;
        }

        .searched-flights-container {
            width: 80vw;
            height: fit-content;
            margin: 80px auto;
            display: flex;
            flex-direction: row;
            justify-content: center;
            gap: 15px;


            .controller {
                height: fit-content;
                width: 300px;
                display: none;
                position: sticky;
                align-items: center;
                padding-bottom: 10px;

                .controller-container {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    border: 1px solid rgba(71, 71, 71, 0.164);
                    background-color: #ffffff;
                    border-radius: 2px;

                    .filter-flights {
                        padding-top: 10px;
                        width: 100%;
                        font-size: 14px;
                        color: rgb(48, 48, 48);

                        .ct-title {
                            font-size: 15px;
                            font-weight: 600;
                            font-family: 'Open Sans', sans-serif;
                            color: rgba(0, 0, 0, 1);
                            margin: 10px 0px 10px 20px;
                        }

                        .flight-count {
                            font-size: 15px;
                            font-family: 'Open Sans', sans-serif;
                            font-weight: 500;
                            color: rgba(0, 0, 0, 0.705);
                        }

                        .flight-filter-box {
                            padding: 10px 20px;

                            .carrier {
                                display: flex;
                                flex-direction: row;
                                padding-bottom: 5px;
                                justify-content: start;
                                gap: 10px;

                                input {
                                    cursor: pointer;
                                }

                                label {
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                }

                .sort-by-price-container {
                    margin-top: 10px;
                    width: 100%;
                    background-color: #ffffff;
                    border: 1px solid rgba(71, 71, 71, 0.123);
                    padding: 10px 20px;
                    border-radius: 2px;

                    .sp-title {
                        font-size: 15px;
                        font-family: 'Open Sans', sans-serif;
                        font-weight: 600;
                        color: rgba(0, 0, 0, 1);
                        margin-bottom: 10px;
                    }

                    .radio-sort {
                        font-size: 14px;
                        display: flex;
                        flex-direction: row;
                        justify-content: start;
                        gap: 10px;
                        margin-bottom: 5px;

                        label {
                            font-family: 'Open Sans', sans-serif;
                        }

                        .lb2 {
                            color: rgba(0, 0, 0, 0.5);
                        }
                    }

                }
            }

            .flights {
                width: 100%;
                height: fit-content;
                border-radius: 10px;
                font-family: "Roboto";

                .flight {
                    width: 100%;
                    height: fit-content;
                    background: white;
                    border-radius: 2px;
                    padding: 0px 0px 15px 0px;
                    border: 1px solid rgba(71, 71, 71, 0.164);
                    margin-bottom: 20px;
                    //box-shadow: 0px 2px 5px 1px rgba(51, 51, 51, 0.1);
                    transition: all 0.4s ease-in-out;

                    .carrier-info {
                        display: flex;
                        flex-direction: row;
                        border-radius: 2px;
                        background: linear-gradient(45deg, rgba(255, 221, 189, 0.062), rgba(255, 208, 0, 0.04));

                        .carriers {
                            width: 100%;
                            height: 70px;
                            display: flex;
                            flex-direction: row;
                            border-bottom: 1px solid rgba(71, 71, 71, 0.185);

                            .each-carrier {
                                display: flex;
                                flex-direction: column;
                                align-items: start;
                                padding: 20px 10px 20px 20px;
                                row-gap: 0px;

                                .carrier-company {
                                    display: flex;
                                    flex-direction: row;
                                    align-items: flex-start;
                                    gap: 10px;

                                    .carrier-name {
                                        font-size: 12px;
                                        font-family: 'Roboto', sans-serif;
                                        font-weight: 500;
                                        color: #272727;
                                    }

                                    .carrier-logo {
                                        width: 30px;
                                        height: 30px;
                                        background-color: #ffffff;
                                        display: flex;
                                        justify-content: center;
                                        align-items: center;
                                        overflow: hidden;
                                        border: none;

                                        /* Hide anything that goes outside the circle */
                                        img {
                                            width: 30px;
                                            height: 30px;
                                            object-fit: cover;
                                        }
                                    }
                                }

                                .carrier-model {
                                    font-size: 8px;
                                    font-family: 'Roboto', sans-serif;
                                    font-weight: 500;
                                    margin-left: 38px;
                                    margin-top: -15px;
                                    color: $carrier-model-color;
                                    padding: 2px 2px;
                                    border-radius: 50px;
                                    //border: 1px solid $carrier-model-color;
                                }
                            }
                        }

                        .pricing {
                            width: 300px;
                            height: 70px;
                            border-bottom: 1px solid rgba(71, 71, 71, 0.185);
                            text-align: right;
                            line-height: 68px;
                            font-weight: 700;
                            color: #424242;
                            padding-right: 25px;
                            font-size: 18px;
                            font-family: sans-serif;

                            .price {
                                font-family: sans-serif;
                                font-size: 30px;
                                color: rgb(27, 27, 27);
                                font-weight: bold;
                            }
                        }
                    }

                    .facilities-info {
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;

                        box-shadow: 0px 5px 5px 0px rgba(51, 51, 51, 0.1);

                        .left-facilities {
                            display: flex;
                            flex-direction: row;

                            @include facilities-design();

                            .checked-bags {

                                display: flex;
                                flex-direction: row;
                                margin: 10px 0px 10px 25px;

                                .icon {
                                    font-size: 10px;
                                    margin-right: 3px;
                                    margin-top: 2px;
                                    color: rgba(36, 36, 36, 0.514);
                                }
                            }

                            .dot-gray {
                                height: 4px;
                                width: 4px;
                                border-radius: 100%;
                                background-color: rgb(161, 161, 161);
                                margin: 16px 8px;
                            }

                            .layover-count {
                                margin-top: 10px;
                            }

                            .layover-duration-total {
                                margin-top: 10px;
                            }
                        }

                        .right-facilities {
                            @include facilities-design();
                            margin-right: 25px;
                            margin-top: 10px;
                        }
                    }

                    .itineraries-data {
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        padding: 0px 25px 20px 25px;
                        margin-top: 10px;
                        //border: 1px solid black;
                        height: fit-content;

                        .left-iti,
                        .right-iti {
                            width: 80%;
                            display: flex;
                            flex-direction: column;
                            justify-content: left;
                            text-align: left;

                            .departure-airport {
                                font-size: 35px;
                                font-weight: 600;
                                font-family: 'Open Sans', sans-serif;
                                color: rgb(39, 39, 39);
                            }

                            .departure-date {
                                font-size: 12px;
                                font-weight: 600;
                                color: rgba(0, 0, 0, 1);
                            }

                            .departure-time {
                                font-size: 16px;
                                font-weight: 600;
                                color: rgb(29, 29, 29);
                            }
                        }

                        .middle-iti {
                            width: 100%;

                            .icon {
                                position: relative;
                                top: -39.55%;
                                width: 24px;
                                height: 24px;
                                rotate: 90deg;
                                color: rgb(48, 48, 48);
                                margin-left: 47%;
                                background-color: white;
                                padding: 0 2px;
                            }

                            .visual-flight-meta-data {
                                display: flex;
                                flex-direction: row;

                                .line {
                                    border-top: 1px solid rgb(48, 48, 48);
                                    margin: 70px auto;
                                    width: 99%;
                                }

                                .left-dot {
                                    height: 5px;
                                    width: 5px;
                                    border-radius: 100%;
                                    margin-top: 67px;
                                    background-color: $visual-color;
                                }

                                .right-dot {
                                    height: 8px;
                                    width: 8px;
                                    border-radius: 100%;
                                    margin-top: 67px;
                                    background-color: $visual-color;
                                }
                            }
                        }

                        .total-flight-duration {
                            margin-top: -90px;
                            text-align: center;
                            color: rgb(48, 48, 48);
                            font-size: 10px;
                            font-weight: 600;
                            font-family: 'Open Sans', sans-serif;
                        }

                        .right-iti {
                            justify-content: right;
                            text-align: right;
                        }
                    }

                    .details {
                        height: fit-content;
                        width: 95%;
                        padding-top: 20px;
                        border-top: 1px solid rgba(0, 0, 0, 0.096);
                        margin: 20px auto 30px auto;

                        .carrier-detail {
                            height: 50px;
                            padding: 10px 0px;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;

                            .travel-date {
                                font-size: 16px;
                                font-weight: 600;
                                color: rgba(0, 0, 0, 0.801);

                                .icon {
                                    font-size: 22px;
                                    padding-top: 10px;
                                }
                            }

                            .company {
                                display: flex;
                                flex-direction: row;
                                justify-content: start;
                                align-items: center;

                                .logo {
                                    height: 33px;
                                    width: 33px;
                                }

                                .name {
                                    font-size: 15px;
                                    font-weight: 600;
                                    color: rgba(0, 0, 0, 1);
                                    margin-left: 10px;
                                    margin-top: -20px;
                                }
                            }

                            .model {
                                font-size: 12px;
                                font-weight: 500;
                                margin-left: 43px;
                                margin-top: -15px;
                                width: fit-content;
                            }
                        }

                        .travel-detail {
                            height: 130px;
                            margin: 0 auto;
                            width: 100%;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            flex-wrap: wrap;
                            padding-left: 30px;
                            border-left: 5px solid rgba(0, 38, 255, 0.082);
                            margin-top: 20px;
                            margin-bottom: 70px;

                            .demographic {
                                display: flex;
                                flex-direction: column;
                                justify-content: flex-start;
                                color: #001e5e;
                                padding: 10px;
                                border-radius: 5px;
                                flex-basis: 15%;

                                .airport-code {
                                    font-size: 30px;
                                    font-weight: 600;
                                }

                                .terminal {
                                    font-size: 12px;
                                    font-weight: 500;
                                }

                                .departure-date {
                                    font-family: 'Open Sans', sans-serif;
                                    font-size: 12px;
                                    font-weight: 600;
                                }

                                .departure-time {
                                    font-size: 16px;
                                    font-weight: 600;
                                }
                            }

                            .visual {
                                height: 100%;
                                width: 100px;
                                display: flex;
                                flex-basis: 20%;
                                flex-direction: column;
                                justify-content: center;
                                align-items: center;

                                .icon {
                                    position: relative;
                                    rotate: 90deg;
                                    left: 40px;
                                    top: 27.9px;
                                    height: 20px;
                                    width: 20px;
                                    z-index: 10;
                                    color: rgb(40, 0, 184);
                                    background-color: white;
                                }

                                .lineJourney {
                                    height: 1.5px;
                                    width: 100px;
                                    position: relative;
                                    top: -20px;
                                    background-color: #002677;
                                    margin-top: 30px;
                                }
                            }

                            .departure {
                                align-items: start;
                            }

                            .arrival {
                                align-items: end;
                            }

                            $colorThemeFairDetails: rgb(0, 0, 0);
                            $fontSizeFareDetails: 11px;
                            $iconSizeFareDetails: 9px;

                            .fareDetailsBySegment {
                                flex-basis: 35%;
                                padding: 20px 0px;
                                font-size: 12px;
                                display: flex;
                                flex-direction: column;
                                gap: 5px;

                                .item {
                                    display: flex;
                                    flex-direction: row;
                                    justify-content: space-between;
                                    align-items: center;
                                    gap: 5px;
                                }

                                p {
                                    margin: 0;
                                    font-size: $fontSizeFareDetails;
                                    color: $colorThemeFairDetails;
                                    font-weight: 600;
                                    font-family: sans-serif;
                                }

                                .icon {
                                    font-size: $iconSizeFareDetails;
                                    margin-top: 1.5px;
                                    color: $colorThemeFairDetails;
                                }
                            }

                            .check-in {}
                        }
                    }

                    .operation-buttons {
                        width: 100%;
                        padding-top: 50px;
                        padding-right: 50px;
                        display: flex;
                        flex-direction: row;
                        justify-content: space-between;
                        margin-top: -25px;
                        margin-left: 25px;
                        color: $operation-button-color;
                        transition: all 0.4s ease-in-out;

                        .operation-button {

                            font-size: 12px;
                            font-weight: 500;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            margin-right: 30px;
                            cursor: pointer;

                            .icon {
                                margin-top: 5px;
                                font-size: 25px;
                            }

                            &:hover {
                                color: rgb(67, 127, 255);

                                .icon {
                                    animation: operation-button-icon-animate 0.5s ease-in-out infinite;
                                }
                            }
                        }

                        .select-button {
                            font-size: 12px;
                            height: 28px;
                            width: 80px;
                            text-align: center;
                            border-radius: 2px;
                            color: $operation-button-color;
                            padding: 5px;
                            font-weight: 600;
                            border: 1px solid $operation-button-color;

                            &:hover {
                                background-color: rgb(14, 90, 255);
                                color: white;
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }
}